<template>
    <div>
      <loading :active='isLoading'  />
      <div class="pro_bg">
        <div class="container product_list">
          <div class="row pt-4 d-flex justify-content-between" >
            <div>
              <button class="btn-dark">
                <router-link :to="{name: 'Orderlist'}"></router-link>
              </button>
              <button class="btn-dark">
                <router-link :to="{name: 'Bsproduct'}"></router-link>
              </button>
         
            </div >
           <div>
            <button class="btn-dark" @click="logout()"></button>
           </div>
   
         </div>
    
         <div class="modal-dialog modal-lg my-5" role="document">
            <div class="modal-content border-0">
              <div class="modal-header bg-dark text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                  <span></span>
                </h5>
              </div>
              <div class="modal-body">
                <div class="row">
                  <div class="col-sm-8">
                    <div class="form-group">
                      <label for="title"></label>
                      <input type="text" class="form-control"  v-model="newCoupon.title"
                      placeholder="">
                    </div>

                    <div class="form-row">
                      <div class="form-group col-md-6">
                        <label for="percent"></label>
                        <input type="text" class="form-control"  v-model="newCoupon.percent"
                        placeholder="">
                      </div>
                      <div class="form-group col-md-6">
                        <label for="due_date"></label>
                        <input type="date" class="form-control"  v-model="newCoupon.due_date">
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="form-group col-md-6">
                        <label for="code"></label>
                        <input type="text" class="form-control"  v-model="newCoupon.code"
                        placeholder="" >
                      </div>
                    </div>
                       <hr>
                    <div class="form-group">
                      <div class="form-check">
                        <input class="form-check-input" type="checkbox"
                        id="is_enabled" v-model="newCoupon.is_enabled"
                        :true_value="1" 
                        :false_value="0">
                          <!-- data01truefalse -->
                        <label class="form-check-label" for="is_enabled" >
                           
                        </label>
                      </div>
                    </div>
                 </div>
                </div>
             </div>
             <div class="modal-footer">
               <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
               <button type="button" class="btn btn-dark" @click="addCoupon"></button>
             </div>
           </div>
         </div>
        <div >

 <!--  -->
       <table class="table mt-4 ">
         <thead>
           <tr>
             <th></th>
             <th></th>
             <th></th>
             <th></th>
             <th></th>
              <th></th>
           </tr>
         </thead>
         <tbody>
           <tr v-for="(item) in coupons" :key="item.id">
    
             <td>{{item.title}}</td>
             <td>{{item.percent}}</td>
             <td>{{item.due_date}}</td>
             <td>{{item.code}}</td>
             <td>
               <button type="button" class="btn btn-outline-dark btn-sm" 
               data-dismiss="modal" @click="openModel(item)"></button>
             </td>
             <td>
               <button class="btn btn-outline-danger btn-sm my-auto" @click="delCoupon(item)">
               <i class="fas fa-trash-alt" style="danger"></i>
               </button>
             </td>

            </tr>
         </tbody>

       </table>



      </div>
   <!--  -->
      <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-dialog modal-lg" role="document">
              <div class="modal-content border-0">
                <div class="modal-body">
                 <div class="row">
                   <div>
                     <div class="form-group">
                       <label for="title"></label>
                       <input type="text" class="form-control" id="title" v-model="tempCoupon.title"
                       placeholder="">
                     </div>

                     <div class="form-row">
                       <div class="form-group col-md-6">
                         <label for="percent"></label>
                         <input type="text" class="form-control" id="category" v-model="tempCoupon.percent"
                         placeholder="">
                      </div>
                      <div class="form-group col-md-6">
                        <label for="due_date"></label>
                        <input type="date" class="form-control" id="date" v-model="tempCoupon.due_date">
                      </div>
                     </div>

                     <div class="form-row">
                      <div class="form-group col-md-6">
                        <label for="code"></label>
                        <input type="text" class="form-control" id="origin_price" v-model="tempCoupon.code"
                         placeholder="" >
                      </div>
                     </div>
                       <hr>
                     <div class="form-group">
                       <div class="form-check">
                         <input class="form-check-input" type="checkbox"
                         id="is_enabled" v-model="newCoupon.is_enabled"
                         :true_value="1" 
                         :false_value="0">
                          <!-- data01truefalse -->
                         <label class="form-check-label" for="is_enabled" >
                            
                         </label>
                       </div>
                     </div>
                   </div>
                 </div>
               </div>

               <div class="modal-footer">
                 <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                 <button type="button" class="btn btn-dark" @click="updateCoupon"></button>
               </div>
              </div>
            </div>

          </div>
        </div>
      </div>
     </div>    
    </div>
    </div>
</template>

<script>
import $ from 'jquery';

export default {

  data() {
      return {
          coupons:[], 
          newCoupon:{
      
          },
          isLoading:false,
      //
        //  due_date:"",
      // timestampv-model
      // watchnewCoupon
          tempCoupon:{

          },
    
      };
  },

  methods: {

    addCoupon(){
      let api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod="post";

      const vm=this; 
      vm.isLoading=true;
      // data
      this.$http[httpMethod](api,{data:vm.newCoupon}).then((response) => {
      // API   
      vm.newCoupon={};
      vm.getCoupon();

      })
    },
    getCoupon(page=1){
      let api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      let httpMethod="get";
      const vm=this; 
      // data
      this.$http[httpMethod](api).then((response) => {
        vm.coupons=response.data.coupons;
        vm.isLoading=false;
      })

    },

    delCoupon(item){
      let api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/coupon/${item.id}`;
      const vm=this;
      vm.isLoading=true;
        // data
        this.$http.delete(api).then((response) => {
        vm.getCoupon();
    
      }) 
    },
      
    openModel(item){
          // const newDate=item.due_date;

          this.tempCoupon=Object.assign({},item);
  
            $("#productModal").modal('show');
    },
      
    updateCoupon() {
      const vm=this;      
      let api=`${process.env.APIPATH}api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
    
      vm.isLoading=true;
      this.$http.put(api,{data:vm.tempCoupon}).then((response) => {
        if(response.data.success){
          $("#productModal").modal('hide');
          vm.getCoupon();
        }else{
            vm.getCoupon();
            $("#productModal").modal('hide');
            alert("")
        }
      }) 
    },
    logout(){
      const api=`${process.env.APIPATH}logout`;
      const vm=this;
      this.$http.post(api).then((response) => {
      // console.log(response.data);
      if(response.data.success){
        console.log("")
        vm.$router.push({name:'Login'})
      }
      })
    },

  },
  created() {
    const token = document.cookie.replace(
      /(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/,
      "$1"
    );

     if (token === "") return
     //tokenreturn
     this.axios.defaults.headers.common["Authorization"] = token;
     this.getCoupon();
  },
    
}
</script>


